@use 'base/variables' as v;
@use 'base/mixins' as m;

//ESTILOS DEL OLVIDE PASSWORD

.olvide{
    padding-top: 5rem;
    @include m.tablet {
        @include m.grid(2, 0);
        padding-top: 10rem;
    }

    .title-olvide{
        padding: .5rem;
    }

    .tagline{
        font-size: 2.3rem;
        line-height: 1;

        @include m.tablet {
            font-size: 3rem;
            text-align: justify;
        }
    }

    
    .boton{
        @include m.tablet {
            display: flex;
            justify-content: center;
        }
    }
    button{
        @include m.colorBoton(v.$azul, v.$blanco);
        
        @include m.tablet {
            padding: 1.5rem 6rem;
        }
    }


    .acciones{
        margin-top: 2rem;
        text-align: center;
        font-weight: 900;

       

        a{
            font-size: 2.1rem;
            color: v.$negroClaro;
            &:hover{
                color: v.$negro;
            }
        }
    }

}

