html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  font-family: "Inconsolata", monospace;
  font-size: 1.6rem;
  min-height: 100%;
  background: linear-gradient(to right, #1d1d1d 0%, #838383 100%);
  background-clip: content-box;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

.contenedor-m {
  max-width: 90rem;
  margin: 0 auto;
}

.contenedor-sm {
  max-width: 70rem;
  margin: 0 auto;
}

.contenedor-usm {
  max-width: 50rem;
  margin: 0 auto;
}

.button {
  padding: 1rem 4rem;
  font-size: 2.3rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  text-align: center;
  display: block;
  width: 100%;
  margin-top: 1rem;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .button {
    width: auto;
  }
}

.title {
  color: #034780;
  text-transform: uppercase;
  font-size: 4.5rem;
  text-align: center;
  letter-spacing: 3px;
  text-shadow: 0px 2px 2px #e6e6e6;
}
@media (min-width: 768px) {
  .title {
    font-size: 7.3rem;
  }
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Teko", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
}

h1 {
  font-size: 6rem;
}

h2 {
  font-size: 5rem;
}

h3 {
  font-size: 4rem;
}

p {
  font-size: 2rem;
  color: #6b7280;
  line-height: 1.8;
  font-family: "Inconsolata", monospace;
}

.tagline {
  font-family: "Teko", sans-serif;
  font-weight: 900;
  color: #bfbfbf;
  font-size: 3rem;
  text-align: center;
}

.descripcion-pagina {
  text-align: center;
}

.login {
  padding-top: 5rem;
}
@media (min-width: 768px) {
  .login {
    display: grid;
    gap: 0;
    grid-template-columns: repeat(2, 1fr);
    padding-top: 10rem;
  }
}
.login .title-login {
  padding: 0.5rem;
}
.login .tagline {
  font-size: 2rem;
  line-height: 1;
}
@media (min-width: 768px) {
  .login .tagline {
    font-size: 3rem;
    text-align: justify;
  }
}
.login .tagline span {
  text-transform: uppercase;
  color: #476DB2;
}
@media (min-width: 768px) {
  .login .boton {
    display: flex;
    justify-content: center;
  }
}
.login button {
  background-color: #2563EB;
  color: #FFFFFF;
  font-family: "Teko", sans-serif;
  transition-property: background-color;
  transition-duration: 0.3s;
}
.login button:hover {
  background-color: #134cca;
  cursor: pointer;
}
@media (min-width: 768px) {
  .login button {
    padding: 1.5rem 6rem;
  }
}
.login .acciones {
  margin-top: 2rem;
  text-align: center;
  font-weight: 900;
}
.login .acciones a {
  font-size: 2.1rem;
  color: #1d1d1d;
}
.login .acciones a:hover {
  color: #000;
}

.olvide {
  padding-top: 5rem;
}
@media (min-width: 768px) {
  .olvide {
    display: grid;
    gap: 0;
    grid-template-columns: repeat(2, 1fr);
    padding-top: 10rem;
  }
}
.olvide .title-olvide {
  padding: 0.5rem;
}
.olvide .tagline {
  font-size: 2.3rem;
  line-height: 1;
}
@media (min-width: 768px) {
  .olvide .tagline {
    font-size: 3rem;
    text-align: justify;
  }
}
@media (min-width: 768px) {
  .olvide .boton {
    display: flex;
    justify-content: center;
  }
}
.olvide button {
  background-color: #2563EB;
  color: #FFFFFF;
  font-family: "Teko", sans-serif;
  transition-property: background-color;
  transition-duration: 0.3s;
}
.olvide button:hover {
  background-color: #134cca;
  cursor: pointer;
}
@media (min-width: 768px) {
  .olvide button {
    padding: 1.5rem 6rem;
  }
}
.olvide .acciones {
  margin-top: 2rem;
  text-align: center;
  font-weight: 900;
}
.olvide .acciones a {
  font-size: 2.1rem;
  color: #1d1d1d;
}
.olvide .acciones a:hover {
  color: #000;
}

.formulario {
  padding-top: 3rem;
}
@media (min-width: 768px) {
  .formulario {
    padding-top: 1rem;
  }
}
@media (min-width: 768px) {
  .formulario h3 {
    font-size: 5rem;
    padding: 2rem;
    padding-bottom: 0;
  }
}
.formulario .campo {
  display: flex;
  align-items: center;
  margin-bottom: 2rem;
  position: relative;
}
@media (min-width: 768px) {
  .formulario .campo {
    margin-bottom: 4rem;
  }
}
.formulario .campo label {
  flex: 0 0 10rem;
  color: #bfbfbf;
}
@media (min-width: 768px) {
  .formulario .campo label {
    flex: 0 0 15rem;
    color: #1a1a1a;
    font-weight: 700;
    font-size: 2.5rem;
  }
}
.formulario .campo input {
  flex: 1;
  padding: 1rem;
  border: none;
  font-size: 2rem;
  background-color: #d8dadf;
  transition: all 300ms ease;
}
.formulario .campo input::placeholder {
  color: #777f8d;
}
.formulario .campo input:focus {
  border: 3px solid #476DB2;
  outline: none;
  box-shadow: 2px 0 1rem #FFFFFF;
}
@media (min-width: 768px) {
  .formulario .campo input {
    font-weight: 600;
  }
}
.formulario .campo .validacion-estado {
  position: absolute;
  right: 0.8rem;
  bottom: 1.4rem;
  z-index: 100;
  font-size: 1.4rem;
  opacity: 0;
}
@media (min-width: 768px) {
  .formulario .campo .validacion-estado {
    font-size: 1.8rem;
  }
}
.formulario .campo-correcto .validacion-estado {
  color: #007800;
  opacity: 1;
}
.formulario .campo-incorrecto .validacion-estado {
  color: #a90000;
  opacity: 1;
}
.formulario .campo-incorrecto input {
  border: 3px solid #a90000;
}

.alerta {
  padding: 1rem 1rem 1rem 2rem;
  text-transform: uppercase;
  font-weight: 900;
  margin: 1rem 0;
  font-size: 1.2rem;
  max-width: 80rem;
}
@media (min-width: 768px) {
  .alerta {
    font-size: 2rem;
  }
}
.alerta.error {
  background-color: #a90000;
  border-left: 0.5rem solid #ffdcdc;
  color: #ffdcdc;
}
.alerta.exito {
  background-color: #007800;
  border-left: 0.5rem solid #abffab;
  color: #abffab;
}